@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;


textarea{
    resize: none !important;
}

@layer components{
    .minibar-icons{
        @apply h-5 w-5
    }
}
body{
    font-family: 'Open Sans', sans-serif
}

.container {
    position: relative;
    width: 300px;
    height: 300px;
}

.image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    backface-visibility: hidden;
}

.middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.container:hover .image {
    opacity: 0.3;
}

.container:hover .middle {
    opacity: 1;
}

.text {
    background-color: #04AA6D;
    color: white;
    font-size: 16px;
    padding: 16px 32px;
}